body {
    background-color: #f5f5f5;
}

.card {
    box-shadow: 0px 0px 10px #8c7a7a;
}

.card-header {
    background-color: #007bff;
    color: white;
}

.card-footer a {
    color: #007bff;
}

.card-footer a:hover {
    color: #0056b3;
    text-decoration: none;
}

.btn-info {
    background-color: #007bff;
    border-color: #007bff;
    color: whitesmoke;
}

.btn-info:hover {
    background-color: #0056b3;
    border-color: #0056b3;
    color: whitesmoke;
}


.jumbotron {
    background-color: #007bff;
    color: white;
}

.jumbotron h1,
.jumbotron p {
    text-align: center;
}

.form-control {
    background-color: #f8f9fa;
    border-radius: 0;
}

.form-control:focus {
    box-shadow: 0px 0px 10px #8c7a7a;
}

/* footer, make it always stick to bottom of the screen */
footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 60px;
    background-color: #f5f5f5;
}

